.App {
  text-align: center;
}
html{
  overflow-x: hidden;
}
*{
  text-align: center;
}
.App-logo {
  height: 5vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.width-50{
  width:50%;
  text-align: center;
  color:#fff;
  font-size: large;
  font-weight: bolder;
}
.width-50:hover{
  text-decoration: none;
}

.acc{
  background-color: #61dafb;
  border-radius: 5px;
}
.nav2{
  width:80%;
  margin:auto;
  border-radius: 5px;
  padding:0;
}
.typeSec{
  display: flex;
  justify-content: center;
}